BACE,cAAA,CACA,WAAA,CACA,aAAA,CClEJ,iBACE,GAAA,sCAAA,CACA,IAAA,4CAAA,CACA,KAAA,sCAAA,CAAA,CAGF,iBACE,QAAA,UAAA,CACA,IAAA,UAAA,CAAA,CAGF,oBACE,KACE,SAAA,CACA,0BAAA,CAEF,GACE,SAAA,CACA,uBAAA,CAAA,CAIJ,eACE,wCAAA,CAGF,eACE,wDAAA,CCpBF,MAEE,0CAAA,CACA,4CAAA,CACA,kDAAA,CACA,sDAAA,CACA,yCAAA,CAGA,mBAAA,CACA,mBAAA,CACA,mBAAA,CAGA,+CAAA,CAGA,4BAAA,CACA,4BAAA,CACA,4BAAA,CAGA,gCAAA,CACA,mDAAA,CACA,mDAAA,CACA,2DAAA,CAGA,8CAAA,CACA,6KAAA,CAIA,2BAAA,CACA,4BAAA,CAGA,kDAAA,CACA,+BAAA,CACA,kCAAA,CACA,gCAAA,CACA,iCAAA,CAGA,iDAAA,CACA,sFAAA,CACA,2FAAA,CACA,6FAAA,CACA,gGAAA,CACA,8FAAA,CACA,wDAAA,CAGA,0GAAA,CACA,kHAAA,CAGA,uHAAA,CACA,6HAAA,CACA,sHAAA,CAGA,sLAAA,CAIA,2LAAA,CAMA,4LAAA,CAIA,iMAAA,CAMA,sMAAA,CAIA,2MAAA,CAMA,8GAAA,CACA,sHAAA,CAEA,iHAAA,CACA,yHAAA,CAEA,mHAAA,CACA,2HAAA,CAGA,uGAAA,CACA,+GAAA,CC9FF,KACI,mCAAA,CACA,uBAAA,CACA,wCAAA,CACA,eAAA,CACA,kCAAA,CACA,iCAAA,CAIJ,EACI,qBAAA,CACA,QAAA,CACA,SAAA,CAIJ,kBACI,sCAAA,CACA,mCAAA,CACA,eAAA,CACA,kBAAA,CAGJ,EACI,iBAAA,CAGJ,EACI,0BAAA,CACA,oBAAA,CAEA,QACI,yBAAA,CAKR,OACI,+DAAA,CACA,kBAAA,CAGJ,SACI,iBAAA,CACA,SAAA,CACA,UAAA,CACA,SAAA,CACA,WAAA,CACA,eAAA,CACA,qBAAA,CACA,kBAAA,CACA,cAAA,CAIJ,WACI,UAAA,CACA,gBAAA,CACA,aAAA,CACA,0BAAA,CAIJ,UACI,cAAA,CACA,WAAA,CAIJ,uCAEI,qBAGI,mCAAA,CACA,sCAAA,CACA,oCAAA,CACA,+BAAA,CAAA,CAKR,2BACI,GACI,uCAAA,CAGJ,IACI,uCAAA,CACA,gCAAA,CAGJ,KACI,uCAAA,CAAA,CAIR,0BACI,wCAAA,CAIJ,qBACI,cAAA,CACA,WAAA,CACA,SAAA,CACA,+BAAA,CACA,aAAA,CACA,gBAAA,CACA,iBAAA,CACA,cAAA,CACA,YAAA,CACA,wBAAA,CACA,uCAAA","sourcesContent":["// Design system variables and mixins\n\n// Breakpoint mixins\n@mixin sm {\n  @media (min-width: 640px) { @content; }\n}\n\n@mixin md {\n  @media (min-width: 768px) { @content; }\n}\n\n@mixin lg {\n  @media (min-width: 1024px) { @content; }\n}\n\n@mixin xl {\n  @media (min-width: 1280px) { @content; }\n}\n\n@mixin xxl {\n  @media (min-width: 1536px) { @content; }\n}\n\n// Typography scales\n$font-size-xs: 0.75rem;   // 12px\n$font-size-sm: 0.875rem;  // 14px\n$font-size-base: 1rem;    // 16px\n$font-size-lg: 1.125rem;  // 18px\n$font-size-xl: 1.25rem;   // 20px\n$font-size-2xl: 1.5rem;   // 24px\n$font-size-3xl: 1.875rem; // 30px\n$font-size-4xl: 2.25rem;  // 36px\n$font-size-5xl: 3rem;     // 48px \n\n// Button gradients\n$button-gradient-primary: linear-gradient(to right, var(--color-lime-300, #a3e635), var(--color-emerald-400, #34d399));\n$button-gradient-primary-hover: linear-gradient(to right, var(--color-lime-400, #84cc16), var(--color-emerald-500, #10b981));\n$button-gradient-secondary: linear-gradient(to right, var(--color-gray-700, #374151), var(--color-gray-800, #1f2937));\n\n:root {\n  // Button gradients\n  --button-gradient-primary: #{$button-gradient-primary};\n  --button-gradient-primary-hover: #{$button-gradient-primary-hover};\n  --button-gradient-secondary: #{$button-gradient-secondary};\n} \n\n// Breakpoint mixins\n@mixin sm {\n  @media (min-width: 640px) { @content; }\n}\n\n@mixin md {\n  @media (min-width: 768px) { @content; }\n}\n\n@mixin lg {\n  @media (min-width: 1024px) { @content; }\n}\n\n@mixin xl {\n  @media (min-width: 1280px) { @content; }\n}\n\n@mixin xxl {\n  @media (min-width: 1536px) { @content; }\n}\n\n// Typography scales\n$font-size-xs: 0.75rem;   // 12px\n$font-size-sm: 0.875rem;  // 14px\n$font-size-base: 1rem;    // 16px\n$font-size-lg: 1.125rem;  // 18px\n$font-size-xl: 1.25rem;   // 20px\n$font-size-2xl: 1.5rem;   // 24px\n$font-size-3xl: 1.875rem; // 30px\n$font-size-4xl: 2.25rem;  // 36px\n$font-size-5xl: 3rem;     // 48px \n\n// Button gradients\n$button-gradient-primary: linear-gradient(to right, var(--color-lime-300, #a3e635), var(--color-emerald-400, #34d399));\n$button-gradient-primary-hover: linear-gradient(to right, var(--color-lime-400, #84cc16), var(--color-emerald-500, #10b981));\n$button-gradient-secondary: linear-gradient(to right, var(--color-gray-700, #374151), var(--color-gray-800, #1f2937));\n\n:root {\n  // Button gradients\n  --button-gradient-primary: #{$button-gradient-primary};\n  --button-gradient-primary-hover: #{$button-gradient-primary-hover};\n  --button-gradient-secondary: #{$button-gradient-secondary};\n} ","// Consolidated theme tokens\n// Reduces duplication by defining base tokens and theme-specific overrides\n\n// Base theme tokens - shared across all variants\n:root {\n    // Typography\n    --font-family-body: 'Inter', -apple-system, BlinkMacSystemFont, sans-serif;\n    --font-family-heading: 'Montserrat', sans-serif;\n    --font-weight-regular: 400;\n    --font-weight-medium: 500;\n    --font-weight-semibold: 600;\n    --font-weight-bold: 700;\n\n    // Core colors - shared across all variants\n    --color-black: #000000;\n    --color-white: #ffffff;\n    --color-gray-50: #f9fafb;\n    --color-gray-100: #f3f4f6;\n    --color-gray-200: #e5e7eb;\n    --color-gray-300: #d1d5db;\n    --color-gray-400: #9ca3af;\n    --color-gray-500: #6b7280;\n    --color-gray-600: #4b5563;\n    --color-gray-700: #374151;\n    --color-gray-800: #1f2937;\n    --color-gray-900: #111827;\n\n    // Semantic colors (default theme)\n    --color-primary: #4f46e5;\n    --color-primary-light: #6366f1;\n    --color-primary-dark: #4338ca;\n    --color-secondary: #10b981;\n    --color-secondary-light: #34d399;\n    --color-secondary-dark: #059669;\n    --color-accent: #f59e0b;\n    --color-accent-light: #fbbf24;\n    --color-accent-dark: #d97706;\n\n    // Feedback colors\n    --color-success: #10b981;\n    --color-error: #ef4444;\n    --color-warning: #f59e0b;\n    --color-info: #3b82f6;\n\n    // Component base tokens\n    --btn-border-radius: 8px;\n    --card-border-radius: 12px;\n    --input-border-radius: 8px;\n\n    // Animation tokens\n    --transition-fast: 150ms ease;\n    --transition-base: 250ms ease;\n    --transition-slow: 350ms ease;\n\n    // Spacing scale\n    --spacing-xs: 0.25rem;\n    --spacing-sm: 0.5rem;\n    --spacing-md: 1rem;\n    --spacing-lg: 1.5rem;\n    --spacing-xl: 2rem;\n    --spacing-2xl: 3rem;\n    --spacing-3xl: 4rem;\n}\n\n// Sports theme variant\n[data-theme=\"sports\"] {\n    --color-primary: #e11d48;\n    --color-primary-light: #f43f5e;\n    --color-primary-dark: #be123c;\n    --color-secondary: #0284c7;\n    --color-secondary-light: #0ea5e9;\n    --color-secondary-dark: #0369a1;\n    --color-accent: #eab308;\n    --color-accent-light: #facc15;\n    --color-accent-dark: #ca8a04;\n\n    // Component-specific overrides\n    --btn-border-radius: 4px;\n    --card-border-radius: 8px;\n}\n\n// Wellness theme variant\n[data-theme=\"wellness\"] {\n    --color-primary: #0d9488;\n    --color-primary-light: #14b8a6;\n    --color-primary-dark: #0f766e;\n    --color-secondary: #8b5cf6;\n    --color-secondary-light: #a78bfa;\n    --color-secondary-dark: #7c3aed;\n    --color-accent: #ec4899;\n    --color-accent-light: #f472b6;\n    --color-accent-dark: #db2777;\n\n    // Component-specific overrides\n    --btn-border-radius: 24px;\n    --card-border-radius: 16px;\n}\n\n// Gym theme variant\n[data-theme=\"gym\"] {\n    --color-primary: #000000;\n    --color-primary-light: #1f2937;\n    --color-primary-dark: #000000;\n    --color-secondary: #fbbf24;\n    --color-secondary-light: #fcd34d;\n    --color-secondary-dark: #f59e0b;\n    --color-accent: #ef4444;\n    --color-accent-light: #f87171;\n    --color-accent-dark: #dc2626;\n\n    // Typography overrides\n    --font-family-heading: 'Bebas Neue', sans-serif;\n    --font-weight-bold: 800;\n}\n\n// Modern theme variant\n[data-theme=\"modern\"] {\n    --color-primary: #6d28d9;\n    --color-primary-light: #8b5cf6;\n    --color-primary-dark: #5b21b6;\n    --color-secondary: #2563eb;\n    --color-secondary-light: #3b82f6;\n    --color-secondary-dark: #1d4ed8;\n    --color-accent: #f43f5e;\n    --color-accent-light: #fb7185;\n    --color-accent-dark: #e11d48;\n}",":root {\n    // Primary color (blue) scale\n    --color-primary-50: #ebf8ff;\n    --color-primary-100: #bee3f8;\n    --color-primary-200: #90cdf4;\n    --color-primary-300: #63b3ed;\n    --color-primary-400: #4299e1;\n    --color-primary-500: #3498db;\n    --color-primary-600: #2980b9;\n    --color-primary-700: #2c5282;\n    --color-primary-800: #2a4365;\n    --color-primary-900: #1a365d;\n\n    // Accent color (lime) scale - based on #ddff0e\n    --color-accent-50: #faffed;\n    --color-accent-100: #f3ffcc;\n    --color-accent-200: #ebff99;\n    --color-accent-300: #e2ff66;\n    --color-accent-400: #ddff0e;\n    --color-accent-500: #b3cc0b;\n    --color-accent-600: #849909;\n    --color-accent-700: #566606;\n    --color-accent-800: #293303;\n    --color-accent-900: #141a01;\n\n    // Gray scale\n    --color-gray-50: #f9fafb;\n    --color-gray-100: #f3f4f6;\n    --color-gray-200: #e5e7eb;\n    --color-gray-300: #d1d5db;\n    --color-gray-400: #9ca3af;\n    --color-gray-500: #6b7280;\n    --color-gray-600: #4b5563;\n    --color-gray-700: #374151;\n    --color-gray-800: #1f2937;\n    --color-gray-900: #111827;\n\n    // Semantic color mapping\n    --color-brand-primary: var(--color-primary-500);\n    --color-brand-accent: var(--color-accent-400);\n\n    // UI state colors\n    --color-ui-background: var(--color-gray-900);\n    --color-ui-surface: var(--color-gray-800);\n    --color-ui-surface-alt: rgba(31, 41, 55, 0.8);\n    --color-ui-border: var(--color-gray-700);\n\n    // Text colors\n    --color-text-primary: var(--color-gray-50);\n    --color-text-secondary: var(--color-gray-300);\n    --color-text-accent: var(--color-accent-400);\n    --color-text-muted: var(--color-gray-400);\n    --color-text-inverse: var(--color-gray-900);\n\n    // Component colors\n    --color-hero-heading: var(--color-text-primary);\n    --color-hero-paragraph: var(--color-text-secondary);\n    --color-hero-highlight: var(--color-text-accent);\n    --color-hero-button-primary: var(--color-brand-accent);\n    --color-hero-button-primary-hover: var(--color-accent-500);\n    --color-hero-button-secondary-bg: var(--color-ui-surface-alt);\n    --color-hero-tooltip-bg: var(--color-ui-surface);\n\n    // R