ess-background);\n  }\n  \n  [data-theme=\"nutrition\"] & {\n    --component-color: var(--color-nutrition-text);\n    --component-background: var(--color-nutrition-background);\n  }\n}\n\n/**\n * Mixin-Style Class Selectors for Common Theme Patterns\n */\n\n/* Primary Button Theme Pattern */\n.theme-primary-button {\n  /* Default primary button styling */\n  background: linear-gradient(\n    var(--angle-gradient, 90deg),\n    var(--color-primary),\n    var(--color-secondary)\n  );\n  \n  /* Theme overrides will be applied via the parent data-theme attribute */\n  [data-theme=\"gym\"] & {\n    /* Theme-specific gradient will be achieved through CSS variables */\n  }\n}\n\n/* Secondary Button Theme Pattern */\n.theme-secondary-button {\n  /* Default secondary button styling */\n  border: 2px solid var(--color-primary);\n  color: var(--color-primary);\n  \n  /* Theme overrides will be applied via the parent data-theme attribute */\n  [data-theme=\"gym\"] & {\n    /* Theme-specific styles will be achieved through CSS variables */\n  }\n}\n\n/* Card Theme Pattern */\n.theme-card {\n  /* Default card styling */\n  background: var(--card-background);\n  border: 1px solid var(--card-border);\n  \n  /* Theme overrides will be applied via the parent data-theme attribute */\n  [data-theme=\"gym\"] & {\n    /* Theme-specific styles will be achieved through CSS variables */\n  }\n}\n\n/**\n * Helper Classes for Explicit Theme Application\n * These allow adding theme classes directly to elements\n */\n\n/* Apply specific theme */\n.theme-default {\n  --current-theme: \"default\";\n}\n\n.theme-gym {\n  --current-theme: \"gym\";\n}\n\n.theme-sports {\n  --current-theme: \"sports\";\n}\n\n.theme-wellness {\n  --current-theme: \"wellness\";\n}\n\n.theme-nutrition {\n  --current-theme: \"nutrition\";\n} ","/**\n * FitCopilot Theme System\n *\n * This is the main entry point for the theme system.\n * It imports all theme-related files and provides overarching theme variables.\n */\n\n// Design tokens - raw values\n@use './design-system/colors-next.scss' as colors;\n\n// Semantic tokens - meaningful names mapped to design tokens\n@use './design-system/semantic-tokens.scss' as semantic;\n\n// Component-specific tokens\n@use './design-system/button-tokens.scss' as button;\n\n// Theme selectors and patterns\n@use './design-system/theme-selectors.scss' as selectors;\n\n/**\n * Base Theme Variables\n * \n * These variables are inherited and potentially overridden by specific themes.\n */\n:root {\n  // Core colors (in addition to those defined in colors-next.scss)\n  --color-primary: #8b5cf6; // violet-500\n  --color-primary-light: #c4b5fd; // violet-300\n  --color-primary-dark: #6d28d9; // violet-700\n  --color-primary-rgb: 139, 92, 246; // violet-500 RGB\n  --color-primary-dark-rgb: 109, 40, 217; // violet-700 RGB\n\n  --color-secondary: #3b82f6; // blue-500\n  --color-secondary-light: #93c5fd; // blue-300\n  --color-secondary-dark: #1d4ed8; // blue-700\n  --color-secondary-rgb: 59, 130, 246; // blue-500 RGB\n\n  --color-background: #111827; // gray-900\n  --color-background-light: #1f2937; // gray-800\n  --color-background-dark: #030712; // gray-950\n\n  --color-text: #f9fafb; // gray-50\n  --color-text-muted: #9ca3af; // gray-400\n  --color-text-dark: #111827; // gray-900\n\n  --color-border: #374151; // gray-700\n\n  // Typography\n  --font-family-sans: 'Inter', system-ui, -apple-system, BlinkMacSystemFont, sans-serif;\n  --font-family-heading: var(--font-family-sans);\n\n  // Typography Scale\n  --font-size-xs: 0.75rem; // 12px\n  --font-size-sm: 0.875rem; // 14px\n  --font-size-base: 1rem; // 16px\n  --font-size-lg: 1.125rem; // 18px\n  --font-size-xl: 1.25rem; // 20px\n  --font-size-2xl: 1.5rem; // 24px\n  --font-size-3xl: 1.875rem; // 30px\n  --font-size-4xl: 2.25rem; // 36px\n  --font-size-5xl: 3rem; // 48px\n  --font-size-6xl: 3.75rem; // 60px\n\n  // Font Weights\n  --font-weight-normal: 400;\n  --font-weight-medium: 500;\n  --font-weight-semibold: 600;\n  --font-weight-bold: 700;\n  --font-weight-extrabold: 800;\n\n  // Spacing\n  --spacing-0: 0;\n  --spacing-1: 0.25rem;\n  --spacing-2: 0.5rem;\n  --spacing-3: 0.75rem;\n  --spacing-4: 1rem;\n  --spacing-6: 1.5rem;\n  --spacing-8: 2rem;\n  --spacing-12: 3rem;\n  --spacing-16: 4rem;\n  --spacing-20: 5rem;\n  --spacing-24: 6rem;\n\n  // Border Radius\n  --radius-sm: 0.125rem;\n  --radius-md: 0.25rem;\n  --radius-lg: 0.5rem;\n  --radius-xl: 1rem;\n  --radius-2xl: 1.5rem;\n  --radius-full: 9999px;\n\n  // Shadows\n  --shadow-sm: 0 1px 2px 0 rgba(0, 0, 0, 0.05);\n  --shadow-md: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);\n  --shadow-lg: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);\n  --shadow-xl: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);\n  --shadow-2xl: 0 25px 50px -12px rgba(0, 0, 0, 0.25);\n  --shadow-inner: inset 0 2px 4px 0 rgba(0, 0, 0, 0.06);\n  --shadow-none: 0 0 #0000;\n\n  // Transitions\n  --transition-fast: 150ms;\n  --transition-normal: 250ms;\n  --transition-slow: 350ms;\n  --transition-all: all var(--transition-normal) ease;\n}\n\n/**\n * Theme-Aware Body Class Selectors\n * \n * These provide an alternative way to apply themes using classes\n * rather than data-attributes, which can be useful for certain\n * integration scenarios.\n */\nbody.theme-gym {\n  & > * {\n    --data-theme: gym;\n  }\n}\n\nbody.theme-sports {\n  & > * {\n    --data-theme: sports;\n  }\n}\n\nbody.theme-wellness {\n  & > * {\n    --data-theme: wellness;\n  }\n}\n\nbody.theme-nutrition {\n  & > * {\n    --data-theme: nutrition;\n  }\n}","/* Reset CSS for React App */\n#athlete-dashboard-root {\n  /* Reset box model */\n  box-sizing: border-box;\n  \n  /* Reset margins and padding */\n  margin: 0;\n  padding: 0;\n  width: 100%;\n  \n  /* Ensure our app fills the container */\n  display: block;\n  \n  /* Reset font settings */\n  font-family: var(--font-family-sans);\n  font-size: var(--font-size-base);\n  line-height: 1.5;\n  \n  /* Reset text styles */\n  text-align: left;\n  text-decoration: none;\n  \n  /* Prevent WordPress admin bar from affecting layout */\n  position: relative;\n  z-index: 1;\n  \n  /* Reset all elements inside */\n  & * {\n    box-sizing: border-box;\n  }\n  \n  /* Remove default margins from typography elements */\n  & h1, & h2, & h3, & h4, & h5, & h6, & p, & ul, & ol, & li {\n    margin: 0;\n    padding: 0;\n  }\n  \n  /* Reset list styles */\n  & ul, & ol {\n    list-style: none;\n  }\n  \n  /* Reset links */\n  & a {\n    text-decoration: none;\n    color: inherit;\n    \n    &:hover, &:focus, &:active {\n      outline: none;\n    }\n  }\n  \n  /* Reset buttons */\n  & button {\n    background: none;\n    border: none;\n    padding: 0;\n    cursor: pointer;\n    font-family: inherit;\n    font-size: inherit;\n  }\n  \n  /* Reset images */\n  & img {\n    max-width: 100%;\n    height: auto;\n    display: block;\n  }\n} ","@keyframes float {\n  0% { transform: translate(0, 0) rotate(0deg); }\n  50% { transform: translate(5px, -10px) rotate(5deg); }\n  100% { transform: translate(0, 0) rotate(0deg); }\n}\n\n@keyframes pulse {\n  0%, 100% { opacity: 0.5; }\n  50% { opacity: 0.8; }\n}\n\n@keyframes fadeInUp {\n  from {\n    opacity: 0;\n    transform: translateY(20px);\n  }\n  to {\n    opacity: 1;\n    transform: translateY(0);\n  }\n}\n\n.animate-float {\n  animation: float 10s ease-in-out infinite;\n}\n\n.animate-pulse {\n  animation: pulse 4s cubic-bezier(0.4, 0, 0.6, 1) infinite;\n} ","/**\n * Button Design Tokens\n * Contains all design tokens specific to button components\n */\n\n@use '../design-system' as ds;\n\n:root {\n  // Basic button properties\n  --button-padding-x: var(--spacing-4, 1rem);\n  --button-padding-y: var(--spacing-2, 0.5rem);\n  --button-border-radius: var(--radius-md, 0.375rem);\n  --button-font-weight: var(--font-weight-semibold, 600);\n  --button-transition: 0.3s ease-in-out all;\n  \n  // Button size tokens\n  --size-btn-sm: 36px;\n  --size-btn-md: 44px;\n  --size-btn-lg: 52px;\n  \n  // Button spacing tokens\n  --button-icon-spacing: var(--spacing-2, 0.5rem);\n  \n  // Standard button min-widths\n  --button-sm-min-width: 120px;\n  --button-md-min-width: 160px;\n  --button-lg-min-width: 180px;\n  \n  // Gradient button specific properties\n  --button-gradient-radius: 9999px; // Rounded like hero button\n  --button-gradient-padding-x: var(--spacing-8, 2rem);\n  --button-gradient-padding-y: var(--spacing-4, 1rem);\n  --button-gradient-font-weight: var(--font-weight-bold, 700);\n  \n  // Enhanced transition effects for smoother animations\n  --button-base-transition: 0.3s ease-in-out all;\n  --button-hover-transition: transform 0.3s cubic-bezier(0.34, 1.56, 0.64, 1), \n                           box-shadow 0.3s cubic-bezier(0.34, 1.56, 0.64, 1), \n                           background-image 0.3s ease, \n                           opacity 0.2s ease;\n  --button-transform-up: -2px; // Lift amount on hover\n  --button-transform-down: 1px; // Press amount on active\n  \n  // Hover effect specific properties\n  --button-hover-glow-color: rgba(163, 230, 53, 0.4); // Lime glow (default)\n  --button-hover-glow-radius: 8px;\n  --button-hover-glow-intensity: 0.4;\n  --button-hover-lift-amount: -2px;\n  --button-hover-scale-amount: 1.03;\n  \n  // Optimized shadow variables - Enhanced for better 3D effect\n  --button-shadow-sm: 0 1px 2px rgba(0, 0, 0, 0.05);\n  --button-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);\n  --button-shadow-md: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);\n  --button-shadow-lg: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);\n  --button-shadow-hover: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);\n  --button-shadow-active: 0 5px 10px -3px rgba(0, 0, 0, 0.1), 0 2px 4px -2px rgba(0, 0, 0, 0.05);\n  --button-shadow-focus: 0 0 0 3px rgba(163, 230, 53, 0.4);\n  \n  // Enhanced colored shadows for gradient buttons matching HeroButton\n  --button-shadow-primary: 0 10px 15px -3px rgba(49, 196, 141, 0.25), 0 4px 6px -2px rgba(49, 196, 141, 0.1);\n  --button-shadow-primary-hover: 0 20px 25px -5px rgba(49, 196, 141, 0.25), 0 10px 10px -5px rgba(49, 196, 141, 0.1);\n  \n  // Gradient backgrounds\n  --button-gradient-primary: linear-gradient(to right, var(--color-lime-300, #a3e635), var(--color-emerald-400, #34d399));\n  --button-gradient-primary-hover: linear-gradient(to right, var(--color-lime-400, #84cc16), var(--color-emerald-500, #10b981));\n  --button-gradient-secondary: linear-gradient(to right, var(--color-gray-700, #374151), var(--color-gray-800, #1f2937));\n  \n  // Theme-specific gradients - Gym theme\n  --button-gradient-gym-primary: linear-gradient(to right, \n    var(--color-gym-primary-light, var(--color-lime-300, #a3e635)), \n    var(--color-gym-primary, var(--color-emerald-400, #34d399))\n  );\n  --button-gradient-gym-primary-hover: linear-gradient(to right, \n    var(--color-gym-primary, var(--color-lime-400, #84cc16)), \n    var(--color-gym-primary-dark, var(--color-emerald-500, #10b981))\n  );\n  \n  // Theme-specific gradients - Sports theme\n  --button-gradient-sports-primary: linear-gradient(to right, \n    var(--color-sports-primary-light, var(--color-blue-300, #93c5fd)), \n    var(--color-sports-primary, var(--color-blue-500, #3b82f6))\n  );\n  --button-gradient-sports-primary-hover: linear-gradient(to right, \n    var(--color-sports-primary, var(--color-blue-400, #60a5fa)), \n    var(--color-sports-primary-dark, var(--color-blue-600, #2563eb))\n  );\n  \n  // Theme-specific gradients - Wellness theme\n  --button-gradient-wellness-primary: linear-gradient(to right, \n    var(--color-wellness-primary-light, var(--color-purple-300, #d8b4fe)), \n    var(--color-wellness-primary, var(--color-purple-500, #a855f7))\n  );\n  --button-gradient-wellness-primary-hover: linear-gradient